<template>
  <div class="roster">
    <div class="roster-list">

      <drag
        class="roster__icon"
        v-for="(char, key) in roster"
        :key="key"
        :transfer-data="char"
      >
        <img
          v-if="unlocked.includes(char.id) || char.isFree"
          @click="selectCharacterMethod(char, true)"
          @dblclick="emitAddToTeam(char)"
          :src="`/img/game/${char.facepic}.jpg`"
        />
        <img
          v-else
          class="roster-faded"
          :src="`/img/game/${char.facepic}.jpg`"
        />
      </drag>
    </div>

    <div class="roster-footer">
      <img
        class="roster-list-next roster-list-next-reverse"
        src="/img/icons/next.svg"
      />
      <span v-for="x in Math.floor(roster.length/12)" :key="x" class="roster-points"></span>
      <img class="roster-list-next" src="/img/icons/next.svg" />
    </div>
  </div>
</template>

<script>
import components from "./script/components";
import methods from "./script/methods";

export default {
  computed: {
    roster() {
      return this.$store.state.Roster.data;
    },
    isLoaded() {
      return this.$store.state.Roster.isLoaded;
    },
    unlocked(){
      return this.$store.getters['user/getUserData'].unlocked
    }
  },
  components,
  methods,
  created() {
    this.selectCharacterMethod(this.roster[0]);
  },
};
</script>

<style lang="scss">
@import "@/assets/css/mixin.scss";
@import "./style";
</style>